<template>
  <div>
    <raw-loader :mytxt=" require('!raw-loader!./VueTabulator.vue').default "></raw-loader> 
    {{msg}}  
    <div class="mb-1">
      <button class="mr-1" @click="onAdd">Add</button>
      <button class="mr-1" @click="testData" :disabled="true">Test data</button>
    </div>
    <!-- {{tableData}} -->
    <div ref="table"></div>
    <b-modal ref="my-modal2" id="modal-1" size="sm" :no-close-on-backdrop="true" hide-footer title="BootstrapVue">
      <form @submit.stop.prevent="onSaveModal">
        <div>
          <!-- <pre>{{r}}</pre> -->
          <div class="form-group row">
            <label class="col-sm-4 col-form-label">Id</label>
            <div class="col-sm-8">
              <input type="text" class="form-control" disabled v-model="r.id" />
            </div>
          </div>
          <div class="form-group row">
            <label class="col-sm-4 col-form-label">
              Name
            </label>
            <div class="col-sm-8">
              <input 
                type="text" 
                v-model="r.name"
                name="name"
                v-validate="'required'"                
                :class="{
                    'form-control': true,
                    'ko-hop-le': errors.has('name'),
                }"
                autocomplete="off"
              />
            </div>
          </div>

          <div class="form-group row">
            <label class="col-sm-4 col-form-label">
              Ngay
            </label>
            <div class="col-sm-8">
              <b-form-datepicker 
                v-model="r.ngay" 
                name="ngay"
                v-validate="'required'"
                :date-format-options="{ year: 'numeric', month: 'numeric', day: 'numeric' }"
                locale="vi"
                class="mb-2" >
              </b-form-datepicker>
            </div>
          </div>
          <div class="form-group row">
            <label class="col-sm-4 col-form-label">
              Act
            </label>
            <div class="col-sm-8">
              <b-form-checkbox
                v-model="r.act"
                name="checkbox-1"
                value=true
                unchecked-value=false
                @change="ChangeAct"                
              >
                Active
              </b-form-checkbox>
            </div>
          </div>

          <div class="form-group row">
            <label class="col-sm-4 col-form-label">
              Thành phố {{ errors.has('tp') }}
            </label>
            <div class="col-sm-8">
              <v-select
                v-model="r.tp"
                name="tp"
                v-validate="'required'"
                :reduce="(option) => option.id"
                :options="tps"
                :has-error="errors.has('tp')"
                :class="{ danger: errors.has('tp') }"                
              />
            </div>
          </div>
          <div class="form-group row">
            <label class="col-sm-4 col-form-label">
              Quyền
            </label>
            <div class="col-sm-8">
              <v-select 
                multiple
                name="quyen" 
                v-model="r.quyen" 
                :options="['Admin','User','Editor']" 
                v-validate="'required'"
                :class="{ danger: errors.has('quyen') }"                
              />
            </div>
          </div>
        
        </div>
        <div class="float-right">
          <button type="submit" class="btn btn-primary mr-1 btn-sm">Save</button>
          <button type="button" class="btn btn-sm" @click="hideModal()">Cancel</button>
        </div>
      </form>
    </b-modal>
  </div>
</template>
<script>
import {TabulatorFull as Tabulator} from 'tabulator-tables'; //import Tabulator library
import $ from "jquery"

import VeeValidate from "vee-validate";
import Vue from "vue";
Vue.use(VeeValidate, {
  classes: true,
});

import vSelect from "vue-select";
Vue.component("v-select", vSelect);
import "vue-select/dist/vue-select.css";
import moment from 'moment';
export default {
  data: function () {
    return {
      tabulator: null, //variable to hold your table
      tableData: [], //data for table to display
      msg:"hello world",
      count:8,
      r:{
        id:0,
        ten:'thuc 101',
        ngay:new Date
      },
      tps:[
        { label: 'Huế', id: 'HUE' },
        { label: 'Sài gòn', id: 'SG' },
        { label: 'Đà nẵng', id: 'DN' },
        { label: 'Quảng ngãi', id: 'QN' },
      ],
    }
  },
  mounted(){
    let that=this;

    var dateFormatter = function(cell, formatterParams){
      var value = cell.getValue();
      if(value){
        value = moment(value , "YYYY/MM/DD").format("DD/MM/YYYY");
      }
      return value;
    };

    this.tabulator = new Tabulator(this.$refs.table, {
      data: this.tableData, //link data to table
      reactiveData:true, //enable data reactivity
      height:$(window).innerHeight()-400,//'200px',//$(window).innerHeight()
      //addRowPos:"top",       //top,bottom//when adding a new row, add it to the top of the table
      // columnVertAlign: "middle", //align header contents
      // cellVertAlign: "middle", //vertically center cell contents

      history:true,             //allow undo and redo actions on the table
      pagination:"local",       //paginate the data
      paginationSize:3,         //allow 7 rows per page of data
      paginationCounter:"rows", //display count of paginated rows in footer
      paginationInitialPage:1, //optional parameter to set the initial page to load    
      paginationSizeSelector: [5, 10, 20, 30, 50],
      //autoColumns:true,
      columns: [
        {
          title: "STT",
          formatter: "rownum",
          headerHozAlign: "center",
          hozAlign: "center",
        },
        {
          title: "Cmd",
          headerHozAlign: "center",
          hozAlign: "center",
          headerSort: false,
          width: 75,
          formatter: function (cell, formatterParams, onRendered) {
            onRendered(function () {
              $(cell.getElement()).find("#edit").on("click", cell.getRow(), that.onEdit);
              $(cell.getElement()).find("#del").on("click", cell.getRow(), that.onDel);
            });
            let $div = $("<div>");
            let $btnEdit=  $("<button>", { text: "Edit", class: "mr-1" }).attr("id", "edit");
            let $btnDel=  $("<button>", { text: "Del", class: "mr-1" }).attr("id", "del");
            $div.append($btnEdit);
            $div.append($btnDel);
            return $div[0];
          },
        },
        {
          title: "Id",
          field: "id",
          width: 60,
          //headerFilter: true,
          hozAlign: "center",
        },
        {
          title: "Name",
          field: "name",
          width: 110,
        },

        {
            title: "Ngày",
            headerHozAlign: "center",
            hozAlign: "center",
            field: "ngay",
            width: 100,
            formatter:dateFormatter,
          },
          /*
          Tại sao không hoạt động
          {title:"Act", field:"act",
            headerHozAlign: "center",
            hozAlign: "center",
            formatter:"tickCross", 
            formatterParams:{
              allowEmpty:true,
              allowTruthy:true,
              tickElement:"yes",//"<i class='fa fa-check'></i>",
              crossElement:"no"//"<i class='fa fa-times'></i>",
            }
          },
          */
          {title:"Act", field:"act", 
            headerHozAlign: "center",
            hozAlign: "center",
            formatter:function(cell, formatterParams, onRendered){
              let $chk=$("<input type='checkbox'>").prop("checked",cell.getValue()).attr("disabled", true);
              return $chk[0];
            },
          },
          {
            title: "Thành phố",
            headerHozAlign: "center",
            hozAlign: "center",
            field: "tp",
            width: 130,
            formatter:function(cell, formatterParams, onRendered){
              let tp=cell.getValue();
              let tps=that.tps;
              let index=that.tps.findIndex(x=>x.id==tp);
              return `${tp} - ${that.tps[index].label}`;
            },
          },
          {
            title: "Quyền",
            headerHozAlign: "center",
            hozAlign: "center",
            field: "quyen",
            width: 150,
            formatter:function(cell, formatterParams, onRendered){
              return JSON.stringify(cell.getValue());
            },
          },




      ], //define table columns
    });
    this.tabulator.on("dataLoaded", function(data){
      that.onLoad();
    });
    this.tabulator.on("pageLoaded", function(pageno){
    }); 
    this.tabulator.on("dataChanged", function(data){
    });
  },
  methods:{
    onLoad(){
      let ar=[
        {id:1,name:"thuc 101",ngay:new Date(),act:true,tp:"QN",quyen:[] },
        {id:2,name:"thuc 102",ngay:new Date(),act:false,tp:"QN",quyen:[] },
      ];
      this.count=3;
      ar.forEach(x=>{
        this.tableData.push(x);
      })
    },
    ChangeAct(){
      let act=(this.r.act=='true') ? true : false;
      this.r.act=act;
    },
    testData(){
    },
    onSaveModal(){
      this.$validator.validateAll().then((result) => {
        if (!result) {
          this.toast("Không hợp lệ");
          return;
        }
        let r=JSON.parse(JSON.stringify(this.r));
        if(r.id==0){
          r.id=this.count;
          this.tabulatorAdd(r);
          this.count++;
          this.setPage(1);
        }else{
          this.tabulatorUpdate(r);
        }
        this.hideModal();
      });
    },    
    onAdd(){
      this.r={
        id:0,
        name:"thuc 101",
        ngay:new Date(),
        act:true,
        tp:"QN",
        quyen:[] 
      };
      this.showModal();
    },
    onEdit(e){
      e.preventDefault();
      let row = e.data;
      let id = row.getIndex();
      this.r=JSON.parse(  JSON.stringify(row.getData()) );
      this.showModal();
    },
    onDel(e) {
      e.preventDefault();
      let row = e.data;
      let id = row.getIndex();
      this.tabulatorDel(id);
    },
    getPageMax(){
      return this.tabulator.getPageMax();
    },
    getLastPage(count) {
      let pageSize = this.tabulator.getPageSize();
      let du = count % pageSize;
      return du == 0 ? count / pageSize : (count - du) / pageSize + 1;
    },
    getPageSize() {
      return this.tabulator.getPageSize();
    },
    getPage() {
      return this.tabulator.getPage();
    },
    setPage(_page) {
      this.tabulator.setPage(_page);
    },
    getData() {
      return this.tabulator.getData();
    },
    tabulatorAdd(r){
      this.tableData.unshift(r);
    },
    tabulatorDel(id){
      let index=this.tableData.findIndex(x=>x.id==id);
      this.tableData.splice(index,1);      
    },
    tabulatorUpdate(r){
      let index=this.tableData.findIndex(x=>x.id==r.id);
      this.tableData.splice(index,1,r);    
    },
    tabulatorGetRow(id){
      //let index=this.tabulator.getData().findIndex(x=>x.id==id);
      //let r=this.tabulator.getData()[index];
      //return r;
    },
    toast(msg, variant = null) {
      this.$bvToast.toast(msg, {
        title: `Information`,
        variant: variant,
        solid: true,
      });
    },
    showModal() {
        this.$refs['my-modal2'].show()
    },
    hideModal() {
      this.$refs['my-modal2'].hide()
    },
  }
}
</script>
<style lang="scss">

  $vs-red:red;
  $vs-default:rgba(60, 60, 60, 0.26);
  $vs-colors: (
      lightest: $vs-default,//$vs-red, //rgba(60, 60, 60, 0.26),
      light: rgba(60, 60, 60, 0.5),
      dark: #333,
      darkest: rgba(0, 0, 0, 0.15),
  ) !default;

  //  Global Component Variables
  $vs-component-bg: none !default;
  $vs-component-line-height: 1.8 !default;
  $vs-component-placeholder-color: inherit !default;

  //  Active State
  $vs-state-active-bg: #5897fb !default;
  $vs-state-active-color: #fff !default;

  //  Deselect State
  $vs-state-deselect-bg: #fb5858 !default;
  $vs-state-deselect-color: #fff !default;

  //  Disabled State
  $vs-state-disabled-bg: rgb(248, 248, 248) !default;
  $vs-state-disabled-color: map_get($vs-colors, 'light') !default;
  $vs-state-disabled-controls-color: map_get($vs-colors, 'light') !default;
  $vs-state-disabled-cursor: not-allowed !default;

  //  Borders
  $vs-border-width: 1px !default;
  $vs-border-style: solid !default;
  $vs-border-radius: 4px !default;
  $vs-border-color: map_get($vs-colors, 'lightest') !default;

  //  Component Controls: Clear, Open Indicator
  $vs-controls-color: map_get($vs-colors, 'light') !default;
  $vs-controls-size: 1 !default;
  $vs-controls-deselect-text-shadow: 0 1px 0 #fff !default;

  //  Selected
  $vs-selected-bg: #f0f0f0 !default;
  $vs-selected-border-color: $vs-border-color !default;
  $vs-selected-border-style: $vs-border-style !default;
  $vs-selected-border-width: $vs-border-width !default;

  //  Dropdown
  $vs-dropdown-z-index: 1000 !default;
  $vs-dropdown-min-width: 160px !default;
  $vs-dropdown-max-height: 350px !default;
  $vs-dropdown-box-shadow: 0px 3px 6px 0px map_get($vs-colors, 'darkest') !default;
  $vs-dropdown-bg: #fff !default;

  @import "vue-select/src/scss/vue-select.scss";
  .xyz{
    border: 1px solid red;
  }
  .danger, 
  .danger .dropdown-toggle, 
  .danger .selected-tag {
    background-color: aqua;
    border-radius: 4px;
  }
  .ko-hop-le {
    border-radius: 4px;
    background-color: aqua;
  }
  $textSize: 8.5px; //14px !default; //table text size
  @import "~tabulator-tables/src/scss/tabulator.scss";
  .tabulator-col-title {
    text-align: center;
  }
  .tabulator .tabulator-footer .tabulator-page.active {
    background-color: greenyellow !important;
    color: blue !important;
  }
  .b-calendar .b-calendar-grid-body .col[data-date] .btn {
    width: 24px;
    height: 24px;
    font-size: 10px;
    line-height: 1;
    margin: 0px auto;
    padding: 6px 0;
  }
  .custom-control.custom-checkbox {
    position: relative;
    z-index: 1;
    display: block;
    min-height: 0.9rem;
    padding-left: 1.5rem;
    padding-top: 0.3rem;
  }
  .col-4 input {
    margin-bottom: 1px;
  }
  .form-group {
    margin-bottom: 0.3rem;
  }
  </style>

